import ScrollMagic from 'scrollmagic';
import 'scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap';
import gsap from 'gsap';
import $ from 'jquery';

window.addEventListener('load', () => {
  let design_titles = document.querySelectorAll('.design_item__title');
  // let tl = new TweenlineMax();

  let controller = new ScrollMagic.Controller();

  new ScrollMagic.Scene()
    .addTo(controller);
  for (let design_title of design_titles) {
    let tween = gsap.staggerFromTo($(design_title).children(), 0.5, {y: -30, opacity: 0}, {y: 0, opacity: 1}, 0.1);

    let scene = new ScrollMagic.Scene({triggerElement: design_title, duration: 0, offset: -300})
      .setTween(tween)
      .addTo(controller);
  }


});
